<template>
  <div id="view-restaurant">
    <ul>
      <li>
        <h4>{{name}}</h4>
        <div class="restaurant-infos">
          <p>{{zomato_link}}</p>
          <p>{{price}}</p>
          <p>{{location}}</p>
          <p>{{obs}}</p>
          <p class="coords">{{lat}}</p>
          <p class="coords">{{lon}}</p>
        </div>
      </li>
    </ul>
    <div class="action-buttons">
      <router-link class="back-btn" to="/">Regressar</router-link>
      <router-link class="edit-btn" v-bind:to="{name: 'edit-restaurant', params: {name:name}}">Editar</router-link>
    </div>
    <LeafletMap/>
  </div>
</template>

<script>
import db from '../components/firebaseInit'
import LeafletMap from '../components/LeafletMap.vue'
import { LMap, LTileLayer, LMarker } from 'vue2-leaflet'

export default {
  name: 'view-restaurant',
  components:{
    LeafletMap,
    LMap,
    LTileLayer,
    LMarker
  },

  data () {
    return {
      restaurants: [],
      name: null,
      location: null,
      obs: null,
      price: null,
      zomato_link: null,
      lat: null,
      lon: null
    }
  },
  created () {
    db.collection('restaurants').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
          'restaurant_id': doc.data().restaurant_id,
          'name': doc.data().name,
          'location': doc.data().location,
          'obs': doc.data().obs,
          'price': doc.data().price,
          'zomato_link': doc.data().zomato_link,
          'lat': doc.data().lat,
          'lon': doc.data().lon
        }
        this.restaurants.push(data)
      })
    })
  },
  beforeRouteEnter (to, from, next) {
    db.collection('restaurants').where('name', '==', to.params.name).get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        next(vm => {
          vm.name = doc.data().name
          vm.location = doc.data().location
          vm.price = doc.data().price
          vm.zomato_link = doc.data().zomato_link
          vm.obs = doc.data().obs
          vm.lat = doc.data().lat
          vm.lon = doc.data().lon
        })
      })
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      db.collection('restaurants').where('name', '==', this.$route.params.name).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.name = doc.data().name
          this.location = doc.data().location
          this.price = doc.data().price
          this.obs = doc.data().obs
          this.lat = doc.data().lat
          this.lon = doc.data().lon
        })
      })
    }
  }
}
</script>

<style scoped lang='scss'>

#view-restaurant{
    display: flex;
    margin: auto;
    margin-top: 50px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #c0dcdc;
    width: 30%;
    padding: 40px;
    ul{
      li{
        h4{
          font-size: 30px;
          padding: 10px;
          font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;

        }
        .restaurant-infos{
          padding: 10px;
          font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
          .coords{
            font-size: 10px;
            font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
            display: inline;
          }
        }
      }
    }
}
.action-buttons{
  display: flex;
  .back-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    background: #36544c;
    padding: 10px 20px 10px 20px;
    text-decoration: none;
    color: white;
    margin: 5px;
  }
  .edit-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    background: #ea6060;
    padding: 10px 20px 10px 20px;
    text-decoration: none;
    color: white;
    margin: 5px;
  }
}
@media only screen and (max-width: 500px) {
  #view-restaurant{
    width: 55%;
  }
}
</style>



