<template>
  <div id="edit-restaurant">
    <h3>Editar Restaurante</h3>
    <div class="form-wrapper">
      <form @submit.prevent="updateRestaurant" class="form">
        <label>Nome: </label>
        <input type="text" v-model="name" required>

        <label>Cidade: </label>
        <select v-model="location">
          <option value="Lisboa">Lisboa</option>
          <option value="Barreiro">Barreiro</option>
        </select>

        <label>Preço: </label>
        <select class="radio" v-model="price">
          <option type="radio" name="cost" value="$"> $</option>
          <option type="radio" name="cost" value="$$"> $$</option>
          <option type="radio" name="cost" value="$$$"> $$$</option>
        </select>

        <label>Link Zomato: </label>
        <input type="text" v-model="zomato_link">

        <label>Obs: </label>
        <input type="text" v-model="obs">

        <label>Latitude (xx.xxxxxx): </label>
        <input type="text" v-model="lat" required>
        <label>Longitude (-xx.xxxxxx): </label>
        <input type="text" v-model="lon" required>

        <div class="action-buttons">
          <button type="submit" class="submit-btn">Submeter</button>
          <router-link to="/" class="cancel-btn">Cancelar</router-link>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import db from '../components/firebaseInit'

export default {
  name: 'edit-restaurant',
  data (){
    return {
      restaurant_id: null,
      name: null,
      location: null,
      obs: null,
      price: null,
      zomato_link: null,
      lat: null,
      lon: null
    }
  },
  beforeRouteEnter(to, from, next) {
    db.collection('restaurants').where('name', '==', to.params.name).get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        next(vm => {
          vm.name = doc.data().name
          vm.location = doc.data().location
          vm.price = doc.data().price
          vm.zomato_link = doc.data().zomato_link
          vm.obs = doc.data().obs
          vm.lat = doc.data().lat
          vm.lon = doc.data().lon
        })
      })
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      db.collection('restaurants').where('name', '==', this.$route.params.name).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.name = doc.data().name
          this.location = doc.data().location
          this.price = doc.data().price
        })
      })
    },
    updateRestaurant () {
      db.collection('restaurants').where('name', '==', this.$route.params.name).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          doc.ref.update({
            name: this.name,
            location: this.location,
            price: this.price,
            zomato_link: this.zomato_link,
            obs: this.obs,
            lat: this.lat,
            lon: this.lon
          })
          .then(() => {
            this.$router.push({
              name: 'view-restaurant', params: {name: this.name}
            })
          })
        })
      })
    }
  }
}
</script>

<style scoped lang="scss">
#edit-restaurant{
    width: 30%;
    margin: auto;
    background: #c0dcdc;
    margin-top: 50px;
  h3{
    text-align: center;
    font-size: 30px;
    padding: 25px;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  }
  .form-wrapper{
    .form{
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 300px;
      margin: auto;
      label{
        font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
      }
      input{
        height: 20px;
      }
      .action-buttons{
        display: flex;
        justify-content: center;
        margin: 10px;
        .submit-btn{
          display: flex;
          justify-content: center;
          align-items: center;
          background: #36544c;
          padding: 10px 20px 10px 20px;
          text-decoration: none;
          color: white;
          margin: 5px;
          font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;

        }
        .cancel-btn{
          display: flex;
          justify-content: center;
          align-items: center;
          background: #748585;
          padding: 10px 20px 10px 20px;
          text-decoration: none;
          color: white;
          margin: 5px;
          font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
        }
      }
    }
  }
}
@media only screen and (max-width: 500px) {
  #edit-restaurant{
    width: 95%;
    select{
      background: white;
    }
  }
}
</style>



