<template>
  <div id="new-restaurant">
    <h3>Novo Restaurante</h3>
    <div class="form-wrapper">
      <form @submit.prevent="saveRestaurant" class="form">
        <label>Nome: </label>
        <input type="text" v-model="name" required>

        <label>Cidade manual</label>
        <input type="text" v-model="location">

        <label>Cidade: </label>
        <select v-model="location">
          <option value="Lisboa">Lisboa</option>
          <option value="Barreiro">Barreiro</option>
        </select>
        <label>Preço: </label>
        <select class="radio" v-model="price">
          <option type="radio" name="cost" value="$"> $</option>
          <option type="radio" name="cost" value="$$"> $$</option>
          <option type="radio" name="cost" value="$$$"> $$$</option>
        </select>
        <label>Link Zomato: </label>
        <input type="text" v-model="zomato_link">

        <label>Obs: </label>
        <input type="text" v-model="obs">

        <label>Latitude (xx.xxxxxx): </label>
        <input type="text" v-model="lat" required>
        <label>Longitude (-xx.xxxxxx): </label>
        <input type="text" v-model="lon" required>

        <div class="action-buttons">
          <button type="submit" class="submit-btn">Submeter</button>
          <router-link to="/" class="cancel-btn">Cancelar</router-link>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import db from '../components/firebaseInit'

export default {
  name: 'new-restaurant',
  data (){
    return {
      restaurant_id: null,
      name: null,
      location: null,
      obs: null,
      price: null,
      zomato_link: null,
      lat: null,
      lon: null
    }
  },
  methods: {
    saveRestaurant(){
      db.collection('restaurants').add({
        name: this.name,
        location: this.location,
        price: this.price,
        zomato_link: this.zomato_link,
        obs: this.obs,
        lat: this.lat,
        lon: this.lon
      })
      .then(docRef => this.$router.push('/'))
      .catch(error => console.log(err))
    }
  }
}
</script>

<style scoped lang="scss">

#new-restaurant{
    width: 30%;
    margin: auto;
    background: #c0dcdc;
    margin-top: 50px;
  h3{
    text-align: center;
    font-size: 30px;
    padding: 25px;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  }
  .form-wrapper{
    .form{
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 300px;
      margin: auto;
      input,
      select{
        height: 20px;
        border: 1px solid gray;
      }
      .action-buttons{
        display: flex;
        justify-content: center;
        margin: 10px;
        .submit-btn{
          display: flex;
          justify-content: center;
          align-items: center;
          background: #36544c;
          padding: 10px 20px 10px 20px;
          text-decoration: none;
          color: white;
          margin: 5px;
          font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;

        }
        .cancel-btn{
          display: flex;
          justify-content: center;
          align-items: center;
          background: #748585;
          padding: 10px 20px 10px 20px;
          text-decoration: none;
          color: white;
          margin: 5px;
          font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
        }
      }
    }
  }
}

@media only screen and (max-width: 500px) {
  #new-restaurant{
    width: 95%;
  }
}


</style>



