import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import NewRestaurant from '@/views/NewRestaurant'
import ViewRestaurant from '@/views/ViewRestaurant'
import EditRestaurant from '@/views/EditRestaurant'
import Lisboa from '@/views/Lisboa'
import Barreiro from '@/views/Barreiro'
import NewCity from '../views/NewCity'
import ViewCity from '../views/ViewCity'
import db from '../components/firebaseInit'

Vue.use(Router)

export default new Router({
  data () {
    return {
      restaurants: [],
      cities: [],
      location: null
    }
  },

  created () {
    db.collection('cities').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
          'name': doc.data().name,
          'location': doc.data().location,
          'lat': doc.data().lat,
          'lon': doc.data().lon
        }
        this.cities.push(data)
      })
    })
  },
  beforeRouteEnter (to, from, next) {
    db.collection('cities').where('location', '==', to.params.location).get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        next(vm => {
          vm.name = doc.data().name
          vm.location = doc.data().location
          vm.lat = doc.data().lat
          vm.lon = doc.data().lon
        })
      })
    })
  },

  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      db.collection('cities').where('location', '==', this.$route.params.location).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.name = doc.data().name
          this.location = doc.data().location
          this.lat = doc.data().lat
          this.lon = doc.data().lon
        })
      })
    }
  },

  routes: [
    {
      path: '/lisboa',
      name: 'lisboa',
      component: Lisboa,
      meta: { centerlat: 38.717, centerlon: -9.145, filterByCity: 'Lisboa' }
    },
    {
      path: '/barreiro',
      name: 'barreiro',
      component: Barreiro,
      meta: { centerlat: 38.657091, centerlon: -9.074422, filterByCity: 'Barreiro' }
    },
    {
      path: '/newcity',
      name: 'new-city',
      component: NewCity,
      meta: { centerlat: 38.657091, centerlon: -9.074422 }
    },
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard
    },
    {
      path: '/:location',
      name: 'view-city',
      component: ViewCity,
      meta: { centerlat: 38.657091, centerlon: -9.074422, filterByCity: 'Porto' }
    },

    {
      path: '/new',
      name: 'new-restaurant',
      component: NewRestaurant
    },
    {
      path: '/edit/:name',
      name: 'edit-restaurant',
      component: EditRestaurant
    },
    {
      path: '/:name',
      name: 'view-restaurant',
      component: ViewRestaurant,
      meta: { centerlat: 38.657091, centerlon: -9.074422 }
    }
  ]
})



