<template>
  <div id="dashboard">
    <CitiesContainer/>
    <div class="locations">
      <router-link to="/lisboa"><div class="city hero-bkg-animated ">Lisboa</div></router-link>
      <router-link to="/barreiro"><div class="city hero-bkg-animated ">Barreiro</div></router-link>
    </div>
      <div class="new-restaurant-btn">
        <router-link to="/new">
          Adicionar Restaurante
        </router-link>
      </div>
      <div class="new-restaurant-btn">
        <router-link to="/newCity">
          Adicionar Cidade
        </router-link>
      </div>
    <div class="random-restaurant-element">
      <button class="random-restaurant-btn" @click="randomRestaurant">O restaurante aleatório é:</button>
      <div>
        <span id="restaurant__list__random__result"></span>
      </div>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
import CitiesContainer from '../components/CitiesContainer'
export default {
  name: 'dashboard',
  components: {
    CitiesContainer
  },
  data () {
    return {
      restaurants: [],
    }
  },
  created() {
    db.collection('restaurants').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        //console.log(doc.data());
        const data = {
          'id': doc.id,
          'restaurant_id': doc.data().restaurant_id,
          'name': doc.data().name,
          'location': doc.data().location,
          'obs': doc.data().obs,
          'price': doc.data().price,
          'zomato_link': doc.data().zomato_link
        }
        this.restaurants.push(data)
      })
    })
  },

  methods: {
    randomRestaurant() {
      let random = this.restaurants[Math.floor(Math.random() * this.restaurants.length)]
      document.getElementById('restaurant__list__random__result').innerHTML = random.name + ' <br> ' + random.obs;
    }
  }
}
</script>

<style lang="scss" scoped>
.hero-bkg-animated {
  background: gray url(https://subtlepatterns.com/patterns/geometry2.png) repeat 0 0;
  width: 100%;
  margin: 0;
  text-align: center;
  height: 300px;
  box-sizing: border-box;
  -webkit-animation: slide 20s linear infinite;
}

.hero-bkg-animated h1 {
  font-family: sans-serif;
}

@-webkit-keyframes slide {
    from { background-position: 0 0; }
    to { background-position: -400px 0; }
}
.locations{
  display: flex;
  flex-direction: row;
  justify-content: center;
  a{
    text-decoration: none;
    color: black;
    margin: 50px;
    .city{
    width: 300px;
    height: 300px;
    border: 1px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    }
  }
}
.new-restaurant-btn{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
}
.random-restaurant-element{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 60px;
  .random-restaurant-btn{
    width: 200px;
    height: 80px;
    margin-top: 100px;
    margin-bottom: 20px;
    font-size: 22px;
    border: 1px solid black;
    padding: 0;
    background: transparent;
    &:active{
      color: white;
      background: teal;
    }
  }
  #restaurant__list__random__result{
    font-size: 22px;
  }
}
.container-restaurant{
    display: flex;
    flex-direction: column;
    align-items: center;
    h2{
      font-size: 35px;
      padding: 50px;
    }
  .restaurant__list{
    display: flex;
    flex-direction: column;
    button{
        align-items: center;
        width: 300px;
        height: 50px;
        border-radius: 5px;
        font-size: 18px;
        margin: auto;
        &:hover{
          color: white;
          background-color: black;
        }
    }
    &__random{
      box-shadow: 3px 5px 62px -13px #848686;
      margin: 20px;
      padding: 80px;
      width: 400px;
      margin: auto;
      margin-top: 30px;
      margin-bottom: 30px;
      background: #ffd7b4;
      display: flex;
      flex-direction: column;

      p{
        font-size: 18px;
      }
      #restaurant__list__random__result{
        font-size: 25px;
        color: black;
      }
  }
    &__unique{
      box-shadow: 3px 5px 62px -13px #848686;
      margin: 20px;
      padding: 30px;
      padding-left: 50px;
      width: 400px;
      background: #f7ffef;
        &:nth-child(odd){
        box-shadow: none;
      }
      a{
        text-decoration: none;
        color: black;
      }
      &__text{
        display: flex;
        font-size: 20px;
        align-items: center;
        p{
          font-weight: bold;
          margin-right: 10px;
          font-size: 18px;
        }
      }
    }
  }
}
.view-restaurant{
  width: 200px;
  height: 40px;
  background: teal;
  align-items: center;
  display: flex;
  justify-content: center;

}
.new-restaurant-btn{
  width: 200px;
  height: 50px;
  background: teal;
  display: flex;
  align-items: center;
  justify-content: center;
  a{
    text-decoration: none;
    color: white;
  }
}
@media only screen and (max-width: 500px) {

}



</style>



