<template>
  <div class="map-container">
    <l-map style="height: 700px; width: 900px" :zoom="zoom" :center="[`${$route.meta.centerlat}`, `${$route.meta.centerlon}`]">
      <l-tile-layer :url="url"></l-tile-layer>
      <ul v-for="restaurant in restaurants"  :key="restaurant.id">
        <li v-if="restaurant.location === `${$route.meta.filterByCity}`">
          <l-marker :lat-lng="[`${restaurant.lat}`,`${restaurant.lon}`]">
            <l-popup class="popup">
              {{restaurant.name}}
              <br>
              <a :href="`https://www.google.com/maps/dir//${restaurant.lat},${restaurant.lon}/data=!3m1!4b1!4m2!4m1!3e3`">Direções</a>
            </l-popup>
          </l-marker>
        </li>
      </ul>
    </l-map>
  </div>
</template>

<script>
import db from '../components/firebaseInit'
import {LMap, LTileLayer, LMarker, LPopup } from 'vue2-leaflet';

export default {
  name: 'LeafletMap',
  components: {
    LMap,
    LTileLayer,
    LMarker,
    LPopup,
  },
  data: function() {
    return  {
      restaurants: [],
      cities: [],
      location: null,
      lat: null,
      lon: null,
      map: null,
      tileLayer: null,
      url: 'https://cartodb-basemaps-{s}.global.ssl.fastly.net/rastertiles/voyager/{z}/{x}/{y}.png',
      zoom: 14,
      }
    },
  created(){
    db.collection('restaurants').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
          'name': doc.data().name,
          'location': doc.data().location,
          'lat': doc.data().lat,
          'lon': doc.data().lon
        }
        this.restaurants.push(data)
      })
    })
  },
}
</script>

<style lang="scss">
.leaflet-popup-content-wrapper{
  width: 120px;
}
.popup{
  color: teal;
  font-size: 14px;
  font-weight: bold;
}
.map-container{
  display: flex;
  max-width: 95%;
  box-shadow: 10px 10px 50px 5px rgba(0,0,0,0.75);
}
</style>



