<template>
  <div id="cities-container">
      <div class="container-restaurant">
        <div class="restaurant--wrap">
            <ul class="restaurant__list" v-for="city in cities"  :key="city.location">
              <li class="restaurant__list__unique" >
                <div class="restaurant-text-container">
                  <span class="restaurant__list__unique__text">
                    {{city.location}}
                  </span>
                </div>
                  <router-link v-bind:to="{name: 'view-city', params: {location: city.location}}">
                    <div class="view-restaurant-btn">
                      Ver Cidade
                    </div>
                  </router-link>
              </li>
            </ul>
        </div>
      </div>
  </div>
</template>

<script>
import db from '../components/firebaseInit'
import LeafletMap from '../components/LeafletMap.vue'

export default {
  name: 'cities-container',
  components: {
    LeafletMap
  },
  data () {
    return {
      restaurants: [],
      cities: [],
      location: null
    }
  },
  created() {
    db.collection('cities').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
          'name': doc.data().name,
          'location': doc.data().location,
          'lat': doc.data().lat,
          'lon': doc.data().lon
        }
        this.cities.push(data)
      })
    })
  },
  beforeRouteEnter (to, from, next) {
    db.collection('cities').where('location', '==', to.params.location).get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        next(vm => {
          vm.name = doc.data().name
          vm.location = doc.data().location
          vm.lat = doc.data().lat
          vm.lon = doc.data().lon
        })
      })
    })
  },

  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      db.collection('cities').where('location', '==', this.$route.params.location).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.name = doc.data().name
          this.location = doc.data().location
          this.lat = doc.data().lat
          this.lon = doc.data().lon
        })
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.new-restaurant-btn{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
}
.random-restaurant-element{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 60px;
  .random-restaurant-btn{
    width: 200px;
    height: 80px;
    margin-top: 100px;
    margin-bottom: 20px;
    font-size: 22px;
    border: 1px solid black;
    padding: 0;
    background: transparent;
    &:active{
      color: white;
