    }
  }
  #restaurant__list__random__result{
    font-size: 22px;
  }
}
.container-restaurant{
    display: flex;
    flex-direction: column;
    align-items: center;
    h2{
      font-size: 35px;
      padding: 50px;
    }
  .restaurant__list{
    display: flex;
    flex-direction: column;
    button{
        align-items: center;
        width: 300px;
        height: 50px;
        border-radius: 5px;
        font-size: 18px;
        margin: auto;
        &:hover{
