          background-color: black;
        }
    }
    &__random{
      box-shadow: 3px 5px 62px -13px #848686;
      margin: 20px;
      padding: 80px;
      width: 400px;
      margin: auto;
      margin-top: 30px;
      margin-bottom: 30px;
      background: #ffd7b4;
      display: flex;
      flex-direction: column;

      p{
        font-size: 18px;
      }
      #restaurant__list__random__result{
        font-size: 25px;
        color: black;
      }
  }
    &__unique{
      box-shadow: 3px 5px 62px -13px #848686;
      margin: 20px;
      padding: 30px;
      padding-left: 50px;
      width: 400px;
      background: #f7ffef;
        &:nth-child(odd){
        box-shadow: none;
      }
      a{
        text-decoration: none;
        color: black;
      }
      &__text{
        display: flex;
        font-size: 20px;
        align-items: center;
        p{
          font-weight: bold;
          margin-right: 10px;
          font-size: 18px;
        }
      }
    }
  }
}
.view-restaurant{
  width: 200px;
  height: 40px;
  background: teal;
  align-items: center;
  display: flex;
  justify-content: center;

}
.new-restaurant-btn{
  width: 200px;
  height: 50px;
  background: teal;
  display: flex;
  align-items: center;
  justify-content: center;
  a{
    text-decoration: none;
    color: white;
  }
}
.container-restaurant{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    h2{
      font-size: 35px;
      padding: 50px;
    }
  .restaurant--wrap{
    margin: 100px;
    margin-top: 50px;
  }
  .map--wrap{
    margin-top: 50px;
  }
  .restaurant__list{
    display: flex;
    flex-direction: column;

    button{
        align-items: center;
        width: 300px;
        height: 50px;
        border-radius: 5px;
        font-size: 18px;
        margin: auto;
        &:hover{
          color: white;
          background-color: black;
        }
    }
    &__random{
      box-shadow: 3px 5px 62px -13px #848686;
      margin: 20px;
      padding: 80px;
      width: 400px;
      margin: auto;
      margin-top: 30px;
      margin-bottom: 30px;
      background: #ffd7b4;
      display: flex;
      flex-direction: column;

      p{
        font-size: 18px;
      }
      #restaurant__list__random__result{
        font-size: 25px;
        color: black;
      }
  }
    &__unique{
      margin: 20px;
      margin-bottom: 10px;
      padding: 30px;
      padding-left: 50px;
      background: #c0dcdc;
      display: flex;
      &:hover{
        box-shadow: 3px 5px 62px -13px #848686;
      }
      .restaurant-text-container{
        width: 60%;
        padding-right: 20px;
      }
      a{
        text-decoration: none;
        color: black;
      }
      &__text{
        display: flex;
        font-size: 18px;
        align-items: center;
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
        p{
          font-weight: bold;
          margin-right: 10px;
          font-size: 18px;
        }
      }
    }
  }
}
.view-restaurant-btn{
  width: 200px;
  height: 40px;
  background: #f0b7b7;
  align-items: center;
  display: flex;
  justify-content: center;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  &:hover{
    background: white;
    color: black;
  }
}
.new-restaurant-btn{
