    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    .restaurant--wrap{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0px;}
}
.map--wrap {
  width: 100%;
}
.map-container{
  margin: auto;
  max-width: 95%;
  box-shadow: none;
}
    .restaurant__list{
      display: flex;
      justify-content: center;
      align-items: center;
      width: 75%;
      .restaurant__list__unique{
        width: 100%;
        padding: 30px;
        .view-restaurant-btn{
          width: 130px;
          height: 40px;
          background: #f0b7b7;
          align-items: center;
          display: flex;
          justify-content: center;
        }
        .restaurant-text-container{
          .restaurant__list__unique__text{
            font-size: 16px;
          }
        }
        .view-restaurant{
          width: 100px;
          font-size: 14px;
        }
      }
    }
}
</style>



